import { Controller } from '@ali/4ever-cangjie';
import { Marks } from '@ali/4ever-mo';


export default function isUnderline(controller: Controller) {
  const underlineMark = controller.query('getActiveMark', Marks.Underline.TYPE);
  // 不识别无意义的 none（一般来自导入文档）
  if (!underlineMark || underlineMark.data.value === 'none') return false;
  return true;
}
